import React from 'react';
//import React, { useState, useEffect, useRef } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  Redirect
} from "react-router-dom";
import ReactMarkdown from 'react-markdown'
import WatercolorPage from './Watercolor.js';
import ProjectPage from './Project.js';
import './App.css';
import { motion } from 'framer-motion'

class App extends React.Component {
    constructor(props) {
      super(props);
      this.state = {scrolled: false,
                      minimalHeader: false,
                  };
    }
    componentDidMount() {
        if (this.state.scrolled === false && window.scrollY >= 50) {
            this.setState({ scrolled: true });
        }
        document.addEventListener('scroll', () => {
            //alert(window.scrollY);
            if (this.state.scrolled === false && window.scrollY >= 50) {
                this.setState({ scrolled: true });
            } else if (this.state.scrolled === true && window.scrollY < 50) {
                this.setState({ scrolled: false });
            }
        });
        const script = document.createElement("script");
        script.src = "/scripts.js";
        script.async = true;
        document.body.appendChild(script);
    }
    render() {
        return (
            <Router className="App">
                <Switch>
                    <Route exact path="/">
                        <Header scrolled={this.state.scrolled} />
                        <WatercolorPage />
                    </Route>
                    <Route path="/products">
                        <Header scrolled={this.state.scrolled} />
                        <WatercolorPage />
                    </Route>
                    <Route path="/textiles">
                        <Header scrolled={this.state.scrolled} />
                        <WatercolorPage />
                    </Route>
                    <Route path="/misc">
                        <Header scrolled={this.state.scrolled} />
                        <WatercolorPage />
                    </Route>
                    <Route path="/about">
                        <Header scrolled={this.state.scrolled} />
                        <WatercolorPage />
                    </Route>
                    <Route path="/osmo">
                        <Header scrolled={this.state.scrolled} minimal={true} />
                        <ProjectPage slug="/osmo"/>
                    </Route>
                    <Route path="/mitrium">
                        <Header scrolled={this.state.scrolled} minimal={true} />
                        <ProjectPage slug="/mitrium"/>
                    </Route>
                    <Route path="/poly">
                        <Header scrolled={this.state.scrolled} minimal={true} />
                        <ProjectPage slug="/poly"/>
                    </Route>
                    <Route path="/hopper">
                        <Header scrolled={this.state.scrolled} minimal={true} />
                        <ProjectPage slug="/hopper"/>
                    </Route>
                    <Route path="/drawings">
                        <Header scrolled={this.state.scrolled} minimal={true} />
                        <ProjectPage slug="/drawings"/>
                    </Route>
                </Switch>
            </Router>
        );
            //<Header scrolled={this.state.scrolled} minimal={true} />
    }
}

function Header(props) {
    return(
        <div className={"header".concat(props.scrolled ? " scrolled" : "").concat(props.minimal ? " minimal" : "")}>
            <div className="header-menu">
                <div className="menu-logo"><Link className="title-link" to="/"><div className="hamburger hamburger-top"></div><div className="hamburger hamburger-bottom"></div></Link></div>
                <div className="header-menu-item">
                    <Link className="header-link" to="/about">About</Link>
                </div>
            </div>
            <h2 className="title-name"><Link className="title-link" to="/">Rose Kirby</Link></h2>
        </div>
    );
}
